import React from 'react';
import App from './App.js'
import DemoEffect from './DemoEffect.js'

class MainContent extends React.Component {

   render() {
      return (
	  	
	
	  <div>
		<App />
		<DemoEffect rotateButton = {true}/>
		</div>
	)
   }
}

export default MainContent;


